#wpadminbar {
  z-index: 99992;
}

body.term-php #wpbody-content > #poststuff form#edittag .acf-columns-2 {
  margin-right: 0;
}
body.term-php #wpbody-content > #poststuff form#edittag .acf-columns-2 .acf-column-1 {
  display: flex;
  flex-direction: column;
  grid-gap: 0 20px;
}
body.term-php #wpbody-content > #poststuff form#edittag .acf-columns-2 .acf-column-1 > * {
  order: 10;
}
body.term-php #wpbody-content > #poststuff form#edittag .acf-columns-2 .acf-column-1 > #titlediv {
  order: 1;
}
body.term-php #wpbody-content > #poststuff form#edittag .acf-columns-2 .acf-column-1 > #side-sortables {
  order: 1;
  margin-left: auto;
}
body.term-php #wpbody-content > #poststuff form#edittag .acf-columns-2 .acf-column-1 > .taxonomy-button {
  order: 2;
  margin-bottom: 20px;
  margin-right: auto;
}
body.term-php #wpbody-content > #poststuff form#edittag .acf-columns-2 .acf-column-1 h2,
body.term-php #wpbody-content > #poststuff form#edittag .acf-columns-2 .acf-column-1 .form-table {
  order: 3;
}
body.term-php #wpbody-content > #poststuff form#edittag .acf-columns-2 .acf-column-1 > #normal-sortables {
  order: 4;
}
body.term-php #wpbody-content > #poststuff form#edittag .acf-columns-2 .acf-column-1 > #poststuff > .termbox-container > #normal-sortables {
  display: none !important;
}
body.term-php #wpbody-content > #poststuff form#edittag .acf-columns-2 .acf-column-2 {
  display: none;
}